export default {
	data() {
		return {
			startTime:'',
			endTime:'',
			currentSelect:'',
			paramsArr:[
				{
					name:'以上课时总量',
					key:'yskszl'
				},{
					name:'在线时长',
					key:'zxsc'
				},{
					name:'掉线次数',
					key:'dxcs'
				},{
					name:'课前预习次数',
					key:'kqyx'
				},{
					name:'课后作业次数',
					key:'khzycs'
				},{
					name:'问题数量',
					key:'wtsl'
				},{
					name:'活跃次数',
					key:'hycs'
				},{
					name:'考试次数',
					key:'kscs'
				},{
					name:'拒收次数',
					key:'jscs'
				},{
					name:'被点名次数',
					key:'bdmcs'
				},
			],
			list: [
				{
					title: '学习情况统计'
				},
				{
					title: '学习成绩'
				}
			],
			active: '0',
			dataArr: [
				{
					title: '整体情况-模拟数据',
					yskszl: '10',
					zxsc: '20',
					dxcs: '15',
					kqyx: '25',
					khzycs: '4',
					wtsl: '011',
					hycs: '25',
					kscs: '2',
					jscs: '6',
					bdmcs: '111',
					isActive:true,
				},{
					title: '腾讯-模拟数据',
					yskszl: '10',
					zxsc: '20',
					dxcs: '15',
					kqyx: '25',
					khzycs: '4',
					wtsl: '011',
					hycs: '25',
					kscs: '2',
					jscs: '6',
					bdmcs: '111'
				},{
					title: '凤凰-模拟数据',
					yskszl: '10',
					zxsc: '20',
					dxcs: '15',
					kqyx: '25',
					khzycs: '4',
					wtsl: '011',
					hycs: '25',
					kscs: '2',
					jscs: '6',
					bdmcs: '111'
				}
			]
		};
	},
	watch: {
		active: {
			immediate:true,
			handler(val) {
				if (val == '0') {
				} else if (val == '1') {

				}
			}
		}
	},
	mounted(){
		this.initData()
	},
	methods: {
		initData(){
			this.$HTTP({
				url:'/statistical/getXxqk',
				params:{
					kssj:this.startTime,
					jssj:this.endTime,
					userid:uni.getStorageSync('userId')
				},
				successCallback:(res)=>{
					console.log(res,'获取参数')
				},
				failCallback:(res)=>{
					
				}
			})
		},
		handleChooseTime(isStartTime){
			if(isStartTime){
				this.currentSelect = 'startTime'
			}else{
				this.currentSelect = 'endTime'
			}
			this.$refs.linkage.show()
		},
		handleCancelChoose(){
			this.$refs.linkage.hide()
		},
		handleChoose({checkArr,checkValue,defaultVal,result}){
			console.log(arguments,'arguments')
			this[this.currentSelect] = result;
			this.$refs.linkage.hide()
		},
		handleCollapse(item,index){
			this.$set(item,'isActive',!item.isActive)
		},
		getData() {}
	}
};
